gsap.registerPlugin(ScrollTrigger);

const spacing = 0,
    cards = gsap.utils.toArray('.cards li'),
    carousel = buildCarousel(cards, spacing),
    scrub = gsap.to(carousel, {
        totalTime: 0,
        duration: 0.5,
        ease: "power3",
        paused: true
    });

function nextSlide() {
    scrubTo(scrub.vars.totalTime + spacing);
}

function prevSlide() {
    scrubTo(scrub.vars.totalTime - spacing);
}

document.querySelector(".next").addEventListener("click", nextSlide);
document.querySelector(".prev").addEventListener("click", prevSlide);

function scrubTo(totalTime) {
    let progress = totalTime / carousel.duration();
    if (progress > 1) {
        progress -= 1;
    } else if (progress < 0) {
        progress += 1;
    }
    scrub.vars.totalTime = progress * carousel.duration();
    scrub.invalidate().restart();
}

function buildCarousel(items, spacing) {
    let overlap = Math.ceil(1 / spacing),
        loopTime = items.length * spacing + 1,
        carousel = gsap.timeline({ paused: true }),
        l = items.length + overlap * 2,
        time = 0,
        i, index, item;

    gsap.set(items, { xPercent: 400, opacity: 0, scale: 0 });

    for (i = 0; i < l; i++) {
        index = i % items.length;
        item = items[index];
        time = i * spacing;
        carousel.fromTo(item, { scale: 0, opacity: 0 }, { scale: 1, opacity: 1, zIndex: 100, duration: 0.5, yoyo: true, repeat: 1, ease: "power1.in", immediateRender: false }, time)
            .fromTo(item, { xPercent: 400 }, { xPercent: -400, duration: 1, ease: "none", immediateRender: false }, time);
    }

    carousel.addLabel("start", 0);
    carousel.addLabel("end", loopTime);

    return carousel;
}
